
.catalog-container {
  padding-top: 80px;
  padding-bottom: 20px;
  padding-left: 40px;
  padding-right: 40px;
  width: 100%;
  max-width: 100%;
  background-color: #eee; }


.catalog-seleted-wrapper {
  display: flex;
  justify-content: space-between;
  margin-bottom: 40px; }

.catalog-seleted-left {
  display: flex;
  width: 100%;
  max-width: 530px; }

.catalog-seleted-right {
  max-width: 300px; }


.btn-sort {
  width: 100%;
  position: relative;
  padding: 3px 0;
  button {
    display: block;
    outline: none;
    border: none;
    @include fontSR;
    font-size: 1.6rem;
    line-height: 105%;
    letter-spacing: 0.12em;
    text-decoration-line: none;
    text-transform: uppercase;
    color: #000;
    padding-right: 30px; }

  .sort-up {
    position: absolute;
    top: 0;
    right: 0;
    width: 10px;
    height: 10px;
    @include img;
    background-image: url('../img/main-page/icons/arrow-left.svg');
    transform: rotate(90deg); }

  .sort-down {
    position: absolute;
    bottom: 0;
    right: 0;
    width: 10px;
    height: 10px;
    @include img;
    background-image: url('../img/main-page/icons/arrow-right.svg');
    transform: rotate(90deg); } }

.arrow-active {
  opacity: 1; }

.arrow-disabled {
  opacity: .4; }





// SELECT


.select__color__top,
.select__gender__top,
.select__subcategory__top {
  display: block; }

.seleted-color,
.seleted-gender,
.seleted-subcategory {
  display: flex;
  width: 100%;
  max-width: 120px;
  cursor: pointer;
  position: relative;
  align-items: center;
  margin-right: 35px;

  & a {
    display: block;
    @include fontSR;
    font-size: 1.6rem;
    line-height: 105%;
    letter-spacing: 0.12em;
    text-decoration-line: none;
    text-transform: uppercase;
    color: #000;
    height: 100%;
    text-decoration: none;
    transition: all .5s;
    @include r(1070) {
      font-size: 1.4rem; }

    &:hover {
      text-decoration: underline; } } }


.select__body-color,
.select__body-gender,
.select__body-subcategory {
  display: none;
  position: absolute;
  top: 100%;
  width: 110%;
  left: -10px;
  z-index: 2;
  padding: 25px 20px;
  border: 1px solid #000;
  background-color: #fff; }


.link__color,
.link__gender,
.link__subcategory {
  position: relative;
  padding: 10px 0;
  box-sizing: border-box; }

.seleted-subcategory {
  max-width: 220px;
  margin-right: 0; }






// js-select
// COLOR
.seleted-color.is-active .select__body-color {
  display: block; }


.select-color__top {
  @include r(768) {
    position: relative;
    width: 100%; } }

.select-color__top:before {
  content: '';
  position: absolute;
  top: calc( 50% - 5px );
  right: 30px;
  width: 10px;
  height: 10px;
  background-image: url('../img/history-page/arrow-select.svg');
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  transition: transform .4s; }

.seleted-color.is-active .select-color__top:before {
  transform: rotate(180deg); }

// GENDER

.seleted-gender.is-active .select__body-gender {
